.nav {
  &__list {
    display: flex;
  }

  &__link {
    white-space: nowrap;

    padding: 23px 0;

    font-size: 12px;
    line-height: 60px;
    font-weight: 500;
    text-decoration: none;
    text-transform: uppercase;
    color: $main-color;

    cursor: pointer;

    transition: color $transition-time $transition-linear;

    &:hover {
      color: $hover-color;
    }

    &--is-active {
      position: relative;

      color: $hover-color;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;

        width: 100%;
        height: 4px;

        background: $hover-color;
        border-radius: 8px;
      }
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
}
